@import url(https://fonts.googleapis.com/css?family=Cormorant&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100vh;
  width: 100vw;
  background: black;
}

.App {
  text-align: center;
  background: url(/yoda-translator/static/media/yoda-background.3682a5f1.jpg);
  background-size: cover;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1 {
  font-family: Cormorant, serif;
  font-weight: 400;
  font-size: calc(20px + 6vw);
  color: white;
  margin: 0;
}

.translator-wrapper {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.translator {
  width: 60%;
  height: 60%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

@media screen and (max-width: 700px) {
  .translator {
    width: 95%;
  }
}

.Input,
.Output {
  height: 50%;
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.Output textarea {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

textarea {
  font-family: Cormorant, serif;
  font-weight: 400;
  width: 90%;
  height: 50%;
  padding: 3%;
  border: white 1px solid;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  color: white;
  font-size: 150%;
  background: rgba(0, 0, 0, 0.7);
}

textarea:focus {
  outline-color: lime;
}

.Button {
  height: 10%;
  width: 50%;
}

input[type="button"] {
  font-family: Cormorant, serif;
  font-weight: 400;
  color: white;
  font-size: 150%;
  border-radius: 10px;
  height: 100%;
  width: 100%;
  cursor: pointer;
  border: none;
  -webkit-transition: 0.1s linear;
  transition: 0.1s linear;
  background: rgba(0, 0, 0, 0.9) url(/yoda-translator/static/media/yoda-head.09ea0a49.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  border: rgba(0, 0, 0, 0.9) 1px solid;
}

input[type="button"]:hover {
  /* transform: scale(1.05); */
  border: lime 1px solid;
}

input[type="button"]:focus {
  outline: none;
}

